body {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  font-family: sans-serif;
  background: #007398;
  color: white;
  padding: 0 30px;
}

.Instructions {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  
  .block {
    background: white;
    border-radius: 3px;
    padding: 20px;
    color: black;
    margin: 20px;
    width: 100%;
    max-width: 600px;
  }

  h1 {
    text-align: center;
  }

  li {
    border: 1px solid white;
    border-radius: 5px;
    padding: 10px;
    text-align: left; 
  }
}

a.link {
  color: cornflowerblue;
  text-decoration: none;
  font-weight: bold;
  border: 2px solid cornflowerblue;
  padding: 5px 10px;
  margin: 0.3em 0;
  display: inline-block;
  border-radius: 3px;
}

a.help {
  color: cornflowerblue;
  text-decoration: none;
  font-weight: bold;
  display: inline-block;
}

a.active {
  color: white;
}

.backButton {
  margin: 1em auto 0 auto;
  padding: 0.3em 1em;
}

.container {
  display: flex;
  justify-content: space-around;
}

.textContainer ul {
  margin: 0;
  padding: 0;
}

.textContainer li {
  list-style: none;
  margin: 0;
  padding: 0.3em;
}

.imageContainer {
  margin-top: 1em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}